
* {
  margin: 0;
  padding: 0;
}
html, body, #app, .mobile {
  height: 100%;
}
.home {
  width: 960px;
  background: #fff;
}
.home-section {
    margin-bottom: 10px;
}
.home-title, .home-count {
    height: 30px;
    line-height: 30px;
}
.home-buttons {
    width: 400px;
}
.mobile {
  background-color: #04082b;
  position: relative;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
}
.mobile ul, .mobile li {
    padding: 0;
    margin: 0;
    list-style: none;
}
.mobile .loading {
    background: url(/static/img/loading.3a61b0f.jpg) no-repeat;
    background-size: 100% auto;
    background-color: #04082b;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1000;
}
.mobile .tabs-wrap {
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    overflow-scrolling: touch;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}
.mobile .tabs {
    height: 46px;
    background: #040B2F;
    width: 659px;
}
.mobile .tab {
    float: left;
    width: 89px;
    height: 30px;
    background-size: contain;
    margin-left: 4px;
    margin-top: 8px;
}
.mobile .list {
    width: 89px;
    position: fixed;
    left: auto;
    top: 39px;
}
.mobile .item {
    display: block;
    background: #00366D;
    border: 1px solid #3D9AFF;
    height: 28px;
    line-height: 28px;
    width: 87px;
    margin-top: 2px;
    font-size: 12px;
    color: #FFFFFF;
    letter-spacing: 0;
    text-align: center;
}
.mobile .item.current {
      background: #1F88F2;
      border: 1px solid #9DCCFF;
}
.mobile .cpzs {
    display: none;
}
.mobile .cpzs.show {
      display: block;
}
.mobile .tab-1 {
    background-image: url(data:image/png;base64,);
}
.mobile .tab-1.current {
      background-image: url(data:image/png;base64,);
}
