.mobile .content {
    display: none;
}
.mobile .content.show {
      display: block;
}
.mobile .content-1 {
    background: url(/static/img/bg_02@3x.b22a7df.png) no-repeat;
    background-size: contain;
    background-position: left bottom;
    width: 100%;
    height: 100%;
    padding-left: 0;
    padding-right: 0;
}
.mobile .content-1 .about-text {
      font-size: 14px;
      color: #FFFFFF;
      letter-spacing: 0;
      text-align: justify;
      text-indent: 25px;
      margin: 0 10px;
      padding-top: 10px;
}
.mobile .content-no-padding {
    padding-left: 0;
    padding-right: 0;
    padding-top: 0;
    margin-top: -46px;
}
.mobile .content-5, .mobile .content-4 {
    margin: 10px 10px 0;
}
